// --- FIREBASE INITIALIZATION ---
// IMPORTANT: Replace with your Firebase project's configuration.
// You can find these in your Firebase project settings.
const firebaseConfig = {
  apiKey: "AIzaSyAhwth6PMIvo8ObnGq3AKZf2w_CNd2VzLY",
  authDomain: "ghanamarket-c83e9.firebaseapp.com",
  projectId: "ghanamarket-c83e9",
  storageBucket: "ghanamarket-c83e9.appspot.com",
  messagingSenderId: "359810222973",
  appId: "1:359810222973:web:b44760dc6c5584ac89da14"
};
firebase.initializeApp(firebaseConfig);

document.addEventListener('DOMContentLoaded', () => {
    // --- STATE ---
    let state = {
        currentUser: null, // Will hold { user: AuthUser, profile: ProfileData }
        items: [], // Will be populated from Supabase
        favorites: [], // Will be populated from user's profile
        sellFormFiles: [], // Holds files for the sell form
        currentView: 'home',
    };

    // --- DOM SELECTORS ---
    const DOMElements = {
        // Views
        homeView: document.getElementById('homeView'),
        loginForm: document.getElementById('loginForm'),
        sellForm: document.getElementById('sellForm'),
        myAdsView: document.getElementById('myAdsView'),
        favoritesView: document.getElementById('favoritesView'),
        adminView: document.getElementById('adminView'),
        importCreditsForm: document.getElementById('importCreditsForm'),
        
        // Auth
        userSection: document.getElementById('userSection'),
        loginSection: document.getElementById('loginSection'),
        showLoginBtn: document.getElementById('showLoginBtn'),
        logoutBtn: document.getElementById('logoutBtn'),
        welcomeText: document.getElementById('welcomeText'),
        creditsDisplay: document.getElementById('creditsDisplay'),
        loginTitle: document.getElementById('loginTitle'),
        loginName: document.getElementById('loginName'),
        loginEmail: document.getElementById('loginEmail'),
        loginPhone: document.getElementById('loginPhone'),
        loginRegion: document.getElementById('loginRegion'),
        loginPassword: document.getElementById('loginPassword'),
        loginSubmit: document.getElementById('loginSubmit'),
        loginToggleBtn: document.getElementById('loginToggleBtn'),
        loginToggleText: document.getElementById('loginToggleText'),
        registerNote: document.getElementById('registerNote'),
        passwordGroup: document.getElementById('passwordGroup'),

        // Navigation Buttons
        homeBtn: document.getElementById('homeBtn'),
        sellBtn: document.getElementById('sellBtn'),
        myAdsBtn: document.getElementById('myAdsBtn'),
        favBtn: document.getElementById('favBtn'),
        adminBtn: document.getElementById('adminBtn'),
        navButtons: document.querySelectorAll('.nav-btn'),

        // Sell Form
        submitBtn: document.getElementById('submitBtn'),
        itemTitle: document.getElementById('itemTitle'),
        itemPrice: document.getElementById('itemPrice'),
        itemCategory: document.getElementById('itemCategory'),
        itemDescription: document.getElementById('itemDescription'),
        itemLocation: document.getElementById('itemLocation'),
        itemWhatsApp: document.getElementById('itemWhatsApp'),
        previewSection: document.getElementById('previewSection'),
        remainingCredits: document.getElementById('remainingCredits'),
        noCreditsMsg: document.getElementById('noCreditsMsg'),

        // Home View
        itemsGrid: document.getElementById('itemsGrid'),
        noItemsMsg: document.getElementById('noItemsMsg'),
        itemCount: document.getElementById('itemCount'),
        showingCount: document.getElementById('showingCount'),
        totalCount: document.getElementById('totalCount'),

        // Favorites
        favCount: document.getElementById('favCount'),
        favoritesCount: document.getElementById('favoritesCount'),
        favoritesGrid: document.getElementById('favoritesGrid'),
        noFavoritesMsg: document.getElementById('noFavoritesMsg'),

        // Import Credits
        importCreditsBtn: document.getElementById('importCreditsBtn'),

        // Search and Filter
        searchInput: document.getElementById('searchInput'),
        categoryFilter: document.getElementById('categoryFilter'),
    };

    const views = [
        DOMElements.homeView, DOMElements.loginForm, DOMElements.sellForm,
        DOMElements.myAdsView, DOMElements.favoritesView, DOMElements.adminView,
        DOMElements.importCreditsForm
    ];

    // --- RENDER & UI FUNCTIONS ---

    const navigateTo = (view) => {
        state.currentView = view;
        views.forEach(v => v.classList.add('hidden'));
        DOMElements.navButtons.forEach(b => b.classList.remove('active'));

        let targetView;
        let activeBtn;

        switch (view) {
            case 'login':
                targetView = DOMElements.loginForm;
                // Reset login form state when navigating to it
                DOMElements.loginEmail.value = '';
                DOMElements.loginPhone.value = '';
                DOMElements.loginPassword.value = '';
                // OTP logic removed
                DOMElements.loginSubmit.classList.remove('hidden');
                DOMElements.loginEmail.type = 'email';
                DOMElements.loginEmail.placeholder = 'Email';
                break;
            case 'sell':
                targetView = DOMElements.sellForm;
                activeBtn = DOMElements.sellBtn;
                updateSellFormUI();
                break;
            case 'myAds':
                targetView = DOMElements.myAdsView;
                activeBtn = DOMElements.myAdsBtn;
                break;
            case 'favorites':
                targetView = DOMElements.favoritesView;
                activeBtn = DOMElements.favBtn;
                renderFavorites();
                break;
            case 'admin':
                targetView = DOMElements.adminView;
                activeBtn = DOMElements.adminBtn;
                break;
            case 'importCredits':
                targetView = DOMElements.importCreditsForm;
                break;
            case 'home':
            default:
                targetView = DOMElements.homeView;
                activeBtn = DOMElements.homeBtn;
                fetchAndRenderItems();
                break;
        }

        if (targetView) targetView.classList.remove('hidden');
        if (activeBtn) activeBtn.classList.add('active');
        window.scrollTo(0, 0);
    };

    const updateAuthUI = () => {
        if (state.currentUser && state.currentUser.profile) {
            DOMElements.userSection.classList.remove('hidden');
            DOMElements.loginSection.classList.add('hidden');
            DOMElements.welcomeText.textContent = `Welcome, ${state.currentUser.profile.username}`;
            DOMElements.sellBtn.classList.remove('hidden');
            DOMElements.myAdsBtn.classList.remove('hidden');

            if (state.currentUser.profile.is_admin) {
                DOMElements.creditsDisplay.textContent = 'Unlimited Credits';
                DOMElements.adminBtn.classList.remove('hidden');
            } else {
                DOMElements.creditsDisplay.textContent = `${state.currentUser.profile.listing_credits} Credits`;
                DOMElements.adminBtn.classList.add('hidden');
            }
        } else {
            DOMElements.userSection.classList.add('hidden');
            DOMElements.loginSection.classList.remove('hidden');
            DOMElements.sellBtn.classList.add('hidden');
            DOMElements.myAdsBtn.classList.add('hidden');
            DOMElements.adminBtn.classList.add('hidden');
        }
    };

    const renderItems = (itemsToRender) => {
        DOMElements.itemsGrid.innerHTML = '';
        if (itemsToRender.length === 0) {
            DOMElements.noItemsMsg.classList.remove('hidden');
        } else {
            DOMElements.noItemsMsg.classList.add('hidden');
        }

        DOMElements.itemCount.textContent = itemsToRender.length;
        DOMElements.showingCount.textContent = itemsToRender.length;
        // For a full app, totalCount might require a separate count query
        DOMElements.totalCount.textContent = itemsToRender.length; 

        itemsToRender.forEach(item => {
            const isFavorite = state.favorites.includes(item.id);
            const itemCard = document.createElement('div');
            itemCard.className = 'card item-card';
            itemCard.dataset.id = item.id;
            itemCard.innerHTML = `
                <img src="${item.images && item.images.length > 0 ? item.images[0] : 'https://placehold.co/300x200?text=No+Image'}" alt="${item.title}" class="item-image">
                <div class="item-content">
                    <div class="item-meta">
                        <span class="category-badge">${item.category}</span>
                        <span class="text-xs text-gray-500">${new Date(item.posted_at).toLocaleDateString()}</span>
                    </div>
                    <h3 class="item-title">${item.title}</h3>
                    <p class="item-description">${item.description}</p>
                </div>
                <div class="item-footer">
                    <div class="item-price">GHS ${item.price}</div>
                    <div class="item-actions">
                        <a href="https://wa.me/${item.whatsapp.replace('+', '')}?text=Hi, I'm interested in your '${item.title}' on Ghana Market." target="_blank" class="btn btn-sm btn-primary">
                            <i data-lucide="message-circle"></i><span>WhatsApp</span>
                        </a>
                        <button class="btn btn-sm btn-secondary favorite-toggle-btn">
                            <i data-lucide="heart" ${isFavorite ? 'fill="red"' : ''}></i>
                        </button>
                    </div>
                </div>
            `;
            DOMElements.itemsGrid.appendChild(itemCard);
        });
        lucide.createIcons();
    };
    
    const updateFavoritesCount = () => {
        const count = state.favorites.length;
        DOMElements.favCount.textContent = count;
        if (DOMElements.favoritesCount) DOMElements.favoritesCount.textContent = count;
    };

    const toggleFavorite = async (itemId) => {
        if (!state.currentUser) {
            alert('Please login to add items to your favorites.');
            navigateTo('login');
            return;
        }

        const favIndex = state.favorites.indexOf(itemId);
        let updatedFavorites = [...state.favorites];

        if (favIndex > -1) {
            updatedFavorites.splice(favIndex, 1);
        } else {
            updatedFavorites.push(itemId);
        }

        try {
            const { error } = await supabaseClient.from('profiles').update({ favorites: updatedFavorites }).eq('id', state.currentUser.user.id);
            if (error) throw error;
            state.favorites = updatedFavorites; // Update local state on success
            state.currentUser.profile.favorites = updatedFavorites;
        } catch (error) {
            console.error("Error updating favorites:", error);
            alert("Could not update favorites. Please try again.");
            return; // Stop if the DB update fails
        }

        updateFavoritesCount();
        
        // Re-render current view to update heart icon
        if (state.currentView === 'home') fetchAndRenderItems(); // Re-fetch to ensure consistency
        if (state.currentView === 'favorites') renderFavorites();
    };

    const renderFavorites = async () => {
        if (!state.favorites || state.favorites.length === 0) {
            DOMElements.favoritesGrid.innerHTML = '';
            DOMElements.noFavoritesMsg.classList.remove('hidden');
            return;
        }

        DOMElements.noFavoritesMsg.classList.add('hidden');
        DOMElements.favoritesGrid.innerHTML = 'Loading favorites...';

        const { data: favoriteItems, error } = await supabaseClient
            .from('items')
            .select('*')
            .in('id', state.favorites);

        if (error) {
            console.error('Error fetching favorite items:', error);
            DOMElements.favoritesGrid.innerHTML = '<p class="text-red-500">Could not load favorites.</p>';
            return;
        }
        
        // Use the main render function
        renderItems(favoriteItems);
    };

    // --- HANDLERS ---

    const handleSellFormSubmit = async (e) => {
        e.preventDefault();
        const user = firebase.auth().currentUser;
        if (!user) {
            alert("You must be logged in to sell an item.");
            return;
        }
        const title = DOMElements.itemTitle.value.trim();
        const price = parseFloat(DOMElements.itemPrice.value);
        const category = DOMElements.itemCategory.value;
        const description = DOMElements.itemDescription.value.trim();
        const location = DOMElements.itemLocation.value.trim();
        const whatsapp = DOMElements.itemWhatsApp.value.trim();
        const deliveryType = document.querySelector('input[name="deliveryType"]:checked').value;
        if (!title || !price || !category || !description || !location || !whatsapp) {
            alert("Please fill out all required fields.");
            return;
        }
        if (state.sellFormFiles.length === 0) {
            alert("Please upload at least one image for your item.");
            return;
        }
        DOMElements.submitBtn.disabled = true;
        DOMElements.submitBtn.innerHTML = `<i data-lucide="loader-2" class="animate-spin"></i><span>Uploading...</span>`;
        lucide.createIcons();
        try {
            // Upload files to Firebase Storage
            const uploadPromises = state.sellFormFiles.map(file => {
                const fileName = `${Date.now()}-${file.name}`;
                const fileRef = firebase.storage().ref().child(`items/${user.uid}/${fileName}`);
                return fileRef.put(file).then(snapshot => fileRef.getDownloadURL());
            });
            const downloadURLs = await Promise.all(uploadPromises);
            // Save item data to Firestore
            await firebase.firestore().collection('items').add({
                title,
                price,
                category,
                description,
                location,
                whatsapp,
                delivery: deliveryType,
                images: downloadURLs,
                seller_id: user.uid,
                status: 'active',
                posted_at: firebase.firestore.FieldValue.serverTimestamp()
            });
            alert("Item listed successfully!");
            resetSellForm();
            navigateTo('home');
        } catch (error) {
            console.error("Error listing item:", error);
            alert(`There was an error listing your item: ${error.message}`);
        } finally {
            DOMElements.submitBtn.disabled = false;
            DOMElements.submitBtn.innerHTML = `<i data-lucide="plus-circle"></i><span>List Item (1 Credit)</span>`;
            lucide.createIcons();
        }
    };

    const resetSellForm = () => {
        DOMElements.sellForm.querySelector('form')?.reset(); // If you wrap it in a form
        state.sellFormFiles = [];
        DOMElements.previewSection.innerHTML = '';
    };

    const updateSellFormUI = () => {
        if (!state.currentUser) return;

        if (state.currentUser.profile?.is_admin) {
            DOMElements.remainingCredits.textContent = 'Unlimited';
            DOMElements.noCreditsMsg.classList.add('hidden');
            DOMElements.submitBtn.disabled = false;
        } else {
            const credits = state.currentUser.profile?.listing_credits || 0;
            DOMElements.remainingCredits.textContent = credits.toString();
            if (credits > 0) {
                DOMElements.noCreditsMsg.classList.add('hidden');
                DOMElements.submitBtn.disabled = false;
            } else {
                DOMElements.noCreditsMsg.classList.remove('hidden');
                DOMElements.submitBtn.disabled = true;
            }
        }
    };

    const handleAuthFormToggle = (isRegister) => {
        if (isRegister) {
            DOMElements.loginTitle.textContent = 'Create Account';
            DOMElements.loginName.classList.remove('hidden');
            DOMElements.loginRegion.classList.remove('hidden');
            DOMElements.registerNote.classList.remove('hidden');
            DOMElements.loginSubmit.textContent = 'Register';
            DOMElements.loginToggleText.textContent = 'Already have an account?';
            DOMElements.loginToggleBtn.textContent = 'Login';
            DOMElements.loginEmail.type = 'email'; // Ensure email field is email type for register
            DOMElements.loginEmail.placeholder = 'Email';
            DOMElements.loginPhone.classList.remove('hidden'); // Show phone for register
            DOMElements.passwordGroup.classList.remove('hidden'); // Show password for register
            DOMElements.loginSubmit.classList.remove('hidden'); // Show register button
        } else {
            DOMElements.loginTitle.textContent = 'Welcome Back';
            DOMElements.loginName.classList.add('hidden');
            DOMElements.loginRegion.classList.add('hidden');
            DOMElements.registerNote.classList.add('hidden');
            DOMElements.loginSubmit.textContent = 'Login';
            DOMElements.loginToggleText.textContent = "Don't have an account?";
            DOMElements.loginToggleBtn.textContent = 'Register';
            DOMElements.loginPhone.classList.add('hidden'); // Hide phone for login initially
            DOMElements.loginEmail.type = 'email';
            DOMElements.loginEmail.placeholder = 'Email';
        }
    };
    
    const handleLogout = async () => {
        try {
            console.log('Logging out with Supabase...');
            const { error } = await supabaseClient.auth.signOut();
            // The onAuthStateChanged listener will handle UI updates and navigation.
            if (error) throw error;
        } catch (error) {
            alert(`Logout failed: ${error.message}`);
        }
    };

    const handleForgotPassword = async () => {
        const email = prompt("Please enter your email address to reset your password:");
        if (!email) return;

        try {
            const { error } = await supabaseClient.auth.resetPasswordForEmail(email, {
                redirectTo: window.location.origin + '/update-password', // You'll need a page at this path
            });
            if (error) throw error;
            alert("Password reset email sent! Please check your inbox.");
        } catch (error) {
            alert(`Error sending password reset email: ${error.message}`);
        }
    };
    
    const fetchAndRenderItems = async () => {
        const searchTerm = DOMElements.searchInput.value.toLowerCase();
        const category = DOMElements.categoryFilter.value;

        try {
            let query = supabaseClient.from('items').select('*').order('posted_at', { ascending: false });

            if (category !== 'all') {
                query = query.eq('category', category);
            }
            
            const { data: items, error } = await query;
            if (error) throw error;

            let filteredItems = items;
            // Client-side search filtering after fetching
            if (searchTerm) {
                filteredItems = items.filter(item => 
                    item.title.toLowerCase().includes(searchTerm) || 
                    item.description.toLowerCase().includes(searchTerm)
                );
            }

            state.items = filteredItems;
            renderItems(state.items);
        } catch (error) {
            console.error("Error fetching items:", error);
            DOMElements.itemsGrid.innerHTML = `<p class="text-center text-red-500">Could not load items. Please try again later.</p>`;
        }
    };

    // Firebase login
    const handleLogin = async () => {
        const email = DOMElements.loginEmail.value.trim();
        const password = DOMElements.loginPassword.value.trim();
        if (!email || !password) {
            alert('Please enter both email and password.');
            return;
        }
        try {
            await firebase.auth().signInWithEmailAndPassword(email, password);
            alert('Login successful!');
            navigateTo('home');
        } catch (error) {
            alert(`Login failed: ${error.message}`);
        }
    };

    // Firebase register
    const handleRegister = async () => {
        const email = DOMElements.loginEmail.value.trim();
        const password = DOMElements.loginPassword.value.trim();
        const name = DOMElements.loginName.value.trim();
        const phone = DOMElements.loginPhone.value.trim();
        const region = DOMElements.loginRegion.value;
        if (!email || !password || !name || !phone || !region) {
            alert('Please fill out all fields for registration.');
            return;
        }
        try {
            const userCredential = await firebase.auth().createUserWithEmailAndPassword(email, password);
            // Optionally save extra user info to Firestore
            await firebase.firestore().collection('users').doc(userCredential.user.uid).set({
                username: name,
                phone: phone,
                region: region,
                email: email
            });
            alert('Registration successful! Please check your email to confirm your account.');
            handleAuthFormToggle(false); // Switch to login view
        } catch (error) {
            alert(`Registration failed: ${error.message}`);
        }
    };

    // --- EVENT LISTENERS ---
    
    // Navigation
    DOMElements.showLoginBtn.addEventListener('click', () => navigateTo('login'));
    DOMElements.homeBtn.addEventListener('click', () => navigateTo('home'));
    DOMElements.sellBtn.addEventListener('click', () => navigateTo('sell'));
    DOMElements.myAdsBtn.addEventListener('click', () => navigateTo('myAds'));
    DOMElements.favBtn.addEventListener('click', () => navigateTo('favorites'));
    DOMElements.adminBtn.addEventListener('click', () => navigateTo('admin'));
    DOMElements.submitBtn.addEventListener('click', handleSellFormSubmit);
    DOMElements.importCreditsBtn.addEventListener('click', () => navigateTo('importCredits'));

    // Auth
    DOMElements.logoutBtn.addEventListener('click', handleLogout);
    DOMElements.loginToggleBtn.addEventListener('click', () => {
        const isRegistering = DOMElements.loginSubmit.textContent === 'Login';
        handleAuthFormToggle(isRegistering);
    });
    DOMElements.loginSubmit.addEventListener('click', (e) => {
        e.preventDefault();
        if (DOMElements.loginSubmit.textContent === 'Login') {
            handleLogin();
        } else {
            handleRegister();
        }
    });
    // DOMElements.forgotPasswordLink.addEventListener('click', (e) => {
    //     e.preventDefault();
    //     handleForgotPassword();
    // });

    // Filtering
    DOMElements.searchInput.addEventListener('input', fetchAndRenderItems);
    DOMElements.categoryFilter.addEventListener('change', fetchAndRenderItems);

    // Item Actions (Event Delegation)
    document.body.addEventListener('click', (e) => {
        const favButton = e.target.closest('.favorite-toggle-btn');
        if (favButton) {
            const itemCard = favButton.closest('.item-card');
            if (itemCard) {
                toggleFavorite(itemCard.dataset.id);
            }
        }
    });

    // --- INITIALIZATION ---
    const init = () => {
        // Initialize Supabase Auth listener to handle session management
        supabaseClient.auth.onAuthStateChange(async (event, session) => {
            const user = session?.user || null;

            if (user) {
                console.log('Auth state changed: User signed in ->', user.id);
                const { data: profile, error } = await supabaseClient.from('profiles').select('*').eq('id', user.id).single();

                if (error && error.code !== 'PGRST116') { // PGRST116 means 0 rows found
                    console.error('Error fetching user profile:', error);
                    state.currentUser = null;
                    state.favorites = [];
                } else if (profile) {
                    state.currentUser = { user, profile };
                    state.favorites = profile.favorites || [];
                } else {
                    // Profile doesn't exist, let's create one as a fallback.
                    // Best practice is a DB trigger in Supabase.
                    console.warn("Profile not found, creating one as a fallback.");
                    const { data: newProfile, error: createError } = await supabaseClient
                        .from('profiles')
                        .insert([{ 
                            id: user.id, 
                            username: user.user_metadata.username || 'New User'
                        }])
                        .select().single();
                    
                    if (createError) {
                        console.error("Error creating user profile:", createError);
                        state.currentUser = { user, profile: null };
                        state.favorites = [];
                    } else {
                        state.currentUser = { user, profile: newProfile };
                        state.favorites = newProfile.favorites || [];
                    }
                }
            } else {
                // User is signed out.
                console.log('Auth state changed: User signed out.');
                state.currentUser = null;
                state.favorites = [];
            }
            // Update UI based on the new auth state
            updateAuthUI();
            // Navigate to home, which will re-render items
            if (state.currentView !== 'login' && state.currentView !== 'register') {
                 navigateTo('home');
            }
        });

        // Initial render on page load
        navigateTo('home');
        lucide.createIcons();
    };

    init();
});